.elementEnter {
  opacity: 0.01;
}
.elementEnterActive {
  opacity: 1;
  -webkit-transition: opacity 0.5s ease-in;
  -o-transition: opacity 0.5s ease-in;
  transition: opacity 0.5s ease-in;
}
.elementLeave {
  opacity: 1;
}
.elementLeaveActive {
  opacity: 0.01;
  -webkit-transition: opacity 0.5s ease-in;
  -o-transition: opacity 0.5s ease-in;
  transition: opacity 0.5s ease-in;
}
.display { display: block; }
.noDisplay { display: none; }


